"use client";

import React, { useState, useEffect } from 'react';

export default function VerifyPage() {
    const [mounted, setMounted] = useState(false);
    const [deepLink, setDeepLink] = useState(null);
    const [isRedirecting, setIsRedirecting] = useState(true);

    useEffect(() => {
        setMounted(true);
        const params = new URLSearchParams(window.location.search);
        const token = params.get('token');

        if (token) {
            const appLink = `cconnect://verify?token=${token}`;
            setDeepLink(appLink);
            window.location.href = appLink;
            const timer = setTimeout(() => setIsRedirecting(false), 3500);
            return () => clearTimeout(timer);
        } else {
            setIsRedirecting(false);
        }
    }, []);

    if (!mounted) return <div className="min-h-screen bg-[#F3F4F6] dark:bg-gray-900" />;

    if (isRedirecting) {
        return (
            <div className="min-h-screen bg-[#F3F4F6] dark:bg-gray-900 flex flex-col items-center justify-center">
                <div className="relative mb-8">
                    <div className="absolute inset-0 rounded-[2rem] bg-blue-500/20 animate-ping"></div>
                    <img src="/images/cConnect.svg" alt="Logo" className="relative w-24 h-24 rounded-[2rem] shadow-2xl" />
                </div>
                <h1 className="text-2xl font-bold flex items-center gap-1">
                    Launching cConnect
                    <span className="flex text-blue-600">
                        <span className="animate-bounce">.</span>
                        <span className="animate-bounce [animation-delay:0.2s]">.</span>
                        <span className="animate-bounce [animation-delay:0.4s]">.</span>
                    </span>
                </h1>
            </div>
        );
    }

    return (
        <div className="bg-[#F3F4F6] dark:bg-gray-900 min-h-screen flex items-center justify-center p-6">
            <div className="bg-white dark:bg-gray-800 rounded-[2.5rem] shadow-2xl max-w-4xl w-full grid md:grid-cols-2 overflow-hidden border border-white dark:border-gray-700">
                
                <div className="p-10 md:p-14 flex flex-col justify-center">
                    <h1 className="text-3xl font-bold mb-4 tracking-tight">Verify Action</h1>
                    <p className="text-gray-500 dark:text-gray-400 mb-8 leading-relaxed">
                        This link requires the cConnect app. If you have the app (Official or Beta) and it didn't open, tap below.
                    </p>
                    
                    {deepLink && (
                        <button 
                            onClick={() => window.location.href = deepLink}
                            className="w-full py-4 bg-[#2D5BFF] hover:bg-blue-700 text-white font-bold rounded-2xl shadow-xl shadow-blue-500/20 transition-all active:scale-[0.98]"
                        >
                            Open cConnect
                        </button>
                    )}
                </div>

                <div className="bg-[#F9FAFB] dark:bg-gray-900/50 p-10 flex flex-col justify-center border-l border-gray-100 dark:border-gray-700">
                    <h2 className="text-[10px] font-black uppercase tracking-[0.2em] text-gray-400 mb-8 text-center">Missing the app?</h2>
                    
                    <div className="space-y-6">
                        <div className="flex flex-col gap-3 max-w-[160px] mx-auto">
                            <a href="#" className="hover:opacity-80 transition-opacity">
                                <img src="https://upload.wikimedia.org/wikipedia/commons/7/78/Google_Play_Store_badge_EN.svg" alt="Google Play" />
                            </a>
                            <a href="#" className="hover:opacity-80 transition-opacity">
                                <img src="https://upload.wikimedia.org/wikipedia/commons/3/3c/Download_on_the_App_Store_Badge.svg" alt="App Store" />
                            </a>
                        </div>

                        <div className="relative flex items-center px-4 py-2">
                            <div className="flex-grow border-t border-gray-200 dark:border-gray-700"></div>
                            <span className="px-3 text-[9px] font-bold text-gray-400 uppercase tracking-widest">or Beta</span>
                            <div className="flex-grow border-t border-gray-200 dark:border-gray-700"></div>
                        </div>

                        <div className="flex flex-col gap-3 max-w-[160px] mx-auto">
                             <a href="#" className="text-[11px] font-bold text-blue-600 dark:text-blue-400 hover:underline flex items-center justify-center gap-2">
                                <span className="text-[14px]">▶</span> Play Store Beta
                             </a>
                             <a href="#" className="text-[11px] font-bold text-gray-900 dark:text-white hover:underline flex items-center justify-center gap-2">
                                <span className="text-[16px]"></span> Join TestFlight
                             </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}